<template>

  <!-- EDM預覽 -->
  <section class="section pt-0">
    <div class="container-fluid">
      <p class="text-muted mb-1">案件簡介</p>

      <!-- EDM風格一 -->
      <div class="y-container">

        <edm1_1 v-if="`${useEDM}` == 1" />
        <edm1_2 v-if="`${useEDM}` == 2" />
        <edm1_4 v-if="`${useEDM}` == 4" />
        <edm1_6 v-if="`${useEDM}` == 6" />
        <edm1_8 v-if="`${useEDM}` == 8" />

      </div>
      <div class="mt-2 mb-2">
        <p class="mb-1">EDM連結</p>
        <div class="input-group mb-3">
          <input type="text" class="form-control" readonly id="txt_output" :value="`${edmUrl}`">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" id="copy" v-on:click="copyurl">複製連結</button>
          </div>
        </div>
      </div>

      <div class="d-flex align-items-center justify-content-center mt-2 pt-2 flex-column">
        <a class="btn btn-primary btn-block" href="/edm-generator/result" type="button">發送EDM</a>
        <button class="btn btn-primary btn-block" v-on:click="varitext" type="button" id="demo">EDM列印</button>
        <button class="btn btn-dark btn-block" type="button">重新製作</button>
      </div>
    </div>
  </section>


</template>

<script>
import $ from 'jquery';
import edm1_1 from '@/components/emdPages/style1-1.vue'
import edm1_2 from '@/components/emdPages/style1-2.vue'
import edm1_4 from '@/components/emdPages/style1-4.vue'
import edm1_6 from '@/components/emdPages/style1-6.vue'
import edm1_8 from '@/components/emdPages/style1-8.vue'

export default {
  components: {
    edm1_1,
    edm1_2,
    edm1_4,
    edm1_6,
    edm1_8
  },
  data() {
    let domain = window.location.origin;
    let tem = sessionStorage.getItem('temStyle');
    let edmId = sessionStorage.getItem('edmId');
    let edmCode = sessionStorage.getItem('edmCode');

    return {
      edmUrl: domain + `/edm/${tem}` + `?id=${edmId}&c=${edmCode}`,

      Packages: [],
      useEDM: 0,
    }
  },
  methods: {
    // 複製URL
    copyurl() {
      var copyText = document.getElementById("txt_output");
      copyText.select();
      copyText.setSelectionRange(0, 99999)
      document.execCommand("copy");
      alert('連結已複製')
      //$('#copy_text').text('連結已複製');
      setTimeout(function () {
        //  $('#copy_text').text('複製連結');
      }, 2000)
    },

    // 列印
    varitext() {
      var text = document.querySelector('edm-style-1');
      print(text)
    },

  }, created() {

    // // 頁面預覽獲取資料
    let edmId = sessionStorage.getItem('edmId');
    let edmCode = sessionStorage.getItem('edmCode');
    let url = `http://21centuryedm.ddns.net/api/edm?id=${edmId}&c=${edmCode}`;

    fetch(url, {})
      .then(res => {
        console.log(res);
        return res.json();
      }).then(formData => {
        console.log(formData);
        let edmLength = formData.data.edm.items.length;
        console.log(edmLength);

        this.useEDM = edmLength;
        console.log(this.useEDM);

        this.Packages = formData.data.edm;
      })

  }
}
</script>
